<template>
<main class="content container">
<div class="content__top content__top--catalog">
<h1 class="content__title">
Каталог
</h1>
<span class="content__info">
152 товара
</span>
</div>

<div class="content__catalog">

<ProductFilter :price-from.sync="filterPriceFrom" :price-to.sync="filterPriceTo"
:category-id.sync="filterCategoryId" :color.sync="filterColor"></ProductFilter>

<section class="catalog">

<div v-if="productsLoader">Загрузка списка товаров</div>
<div v-if="productsLoaderFail">Произошла ошибка, повторите попытку. <button @click.prevent="loadProducts()">Попробовать снова.</button></div>

<ProductList :products="products"></ProductList>

<BasedPagination v-model="page" :count="countProducts" :per-page="productsPerPage">
</BasedPagination>

</section>
</div>
</main>
</template>

<script>
import ProductList from '@/components/ProductList.vue';
import BasedPagination from '@/components/BasedPagination.vue';
import ProductFilter from '@/components/ProductFilter.vue';
import axios from 'axios';
import {API_BASE_URL} from '../config.js';

export default {
  components: { ProductList, BasedPagination, ProductFilter },
  data() {
    return {
      filterPriceFrom: 0,
      filterPriceTo: 0,
      filterCategoryId: 0,
      filterColor: 0,
      page: 1,
      productsPerPage: 3,
      productsData: null,
      colorData: null,
      productsLoader: false,
      productsLoaderFail: false,
    };
  },
  computed: {
    products() {
      return this.productsData ? this.productsData.items.map(product => {
        return {
          ...product,
          image: product.image.file.url,
        }
      }) : [];
    },
    countProducts() {
      return this.productsData ? this.productsData.pagination.total : 0
    },
  },
  methods: {
    loadProducts() {
      this.productsLoader = true;
      this.productsLoaderFail = false,
      clearTimeout(this.loadProductsTimer)
      this.loadProductsTimer = setTimeout(() => {
        axios.get(API_BASE_URL + `/api/products`, {
        params: {
          page: this.page,
          limit: this.productsPerPage,
          colorId: this.filterColor,
          categoryId: this.filterCategoryId,
          minPrice: this.filterPriceFrom,
          maxPrice: this.filterPriceTo,
        }
      })
      .then(response => this.productsData = response.data)
      .catch(() => this.productsLoaderFail = true)
      .then(() => this.productsLoader = false);
      }, 0)
    },
    loadColors() {
      axios.get(API_BASE_URL + `/api/colors`)
      .then(response => this.colorData = response.data);
    },
  },
  watch: {
    page() {
      this.loadProducts();
    },
    filterCategoryId() {
      this.loadProducts()
    },
    filterPriceTo() {
      this.loadProducts()
    },
    filterPriceFrom() {
      this.loadProducts()
    },
    filterColorId() {
      this.loadProducts()
    },
  },
  created() {
    this.loadProducts(); {

    }
    this.loadColors(); {

    }
  }
};
</script>
