<template>
  <li class="cart__item product">
    <div class="product__pic">
      <img :src="item.product.image" width="120" height="120" alt="item.product.title">
    </div>
    <h3 class="product__title">
      {{item.product.title}}
    </h3>
    <span class="product__code">
      Артикул: {{item.product.id}}
    </span>

    <div class="product__counter form__counter">
      <button type="button" aria-label="Убрать один товар" disabled="true" v-if="item.amount === 1">
        <svg width="10" height="10" fill="currentColor">
          <use xlink:href="#icon-"></use>
        </svg>
      </button>
      <button type="button" aria-label="Убрать один товар" v-if="item.amount !== 1" @click.prevent="countProductAmount(item.amount - 1)">
        <svg width="10" height="10" fill="currentColor">
          <use xlink:href="#icon-minus"></use>
        </svg>
      </button>

      <input type="number" v-model.number="amount" name="count" min="1" oninput="validity.valid||(value='');">

      <button type="button" class="plus-amount" aria-label="Добавить один товар" @click.prevent="countProductAmount(item.amount + 1)">
        <svg width="10" height="10" fill="currentColor">
          <use xlink:href="#icon-plus"></use>
        </svg>
      </button>
    </div>

    <b class="product__price">
    {{item.product.price * item.amount | numberFormat}}
    </b>

    <button class="product__del button-del" type="button" aria-label="Удалить товар из корзины" @click.prevent="removeProductFromCart(item.productId)">
      <svg width="20" height="20" fill="currentColor">
        <use xlink:href="#icon-close"></use>
      </svg>
    </button>
  </li>
</template>

<script>

import numberFormat from '@/helpers/numberFormat'
import { mapMutations, mapActions } from 'vuex'
export default {
  props: ['item'],
   filters: {
    numberFormat
  },
  computed: {
    amount: {
      get(){
        return this.item.amount;
      },
      set(value){
        this.$store.dispatch('updateCartProductAmount', {productId: this.item.productId, amount: value})
      },
    },
  },
  methods: {
    ...mapActions(['removeProductFromCart']),
    ...mapMutations({deleteCartProduct: 'deleteCartProduct', countPlus: 'countPlus', countCartProducts: 'countCartProducts'}),
    countProductAmount(amount) {
      this.amount = amount
    }
  }
}
</script>

<style>
input::-webkit-inner-spin-button,
input::-webkit-outer-spin-button {
  -webkit-appearance: none;
}

.product__counter {
  border: none;
}

.plus-amount {
  width: 100%;
}
</style>
