<template>
<ul class="catalog__pagination pagination">
<li class="pagination__item">
<a class="pagination__link pagination__link--arrow" :class="{'pagination__link--disabled'
 : page === 1}" @click.prevent="prevPage(page)">
  <svg width="8" height="14" fill="currentColor">
    <use xlink:href="#icon-arrow-left"></use>
  </svg>
</a>
</li>
<li class="pagination__item" v-for="pageNumber in pages" :key="pageNumber" >
<a href="#" class="pagination__link" :class="{'pagination__link--current'
: pageNumber === page}" @click.prevent="paginate(pageNumber)">
  {{ pageNumber }}
</a>
</li>
<li class="pagination__item">
<a class="pagination__link pagination__link--arrow" href="#" :class="{'pagination__link--disabled'
 : page === 7}" @click.prevent="nextPage(page)">
  <svg width="8" height="14" fill="currentColor">
    <use xlink:href="#icon-arrow-right"></use>
  </svg>
</a>
</li>
</ul>
</template>

<script>
export default {
  model: {
    prop: 'page',
    event: 'paginate',
  },
  props: ['page', 'count', 'perPage'],
  computed: {
    pages() {
      return Math.ceil(this.count / this.perPage);
    },
  },
  methods: {
    paginate(page) {
      this.$emit('paginate', page);
    },
    nextPage(page) {
      this.$emit('paginate', page + 1);
    },
    prevPage(page) {
      this.$emit('paginate', page - 1);
    },
    lastPage() {
      this.btnDisabled = 'true';
    },
  },
};
</script>

<style>
.pagination__link--disabled {
  display: none;
}
</style>
