import BasedFormField from '@/components/BasedFormField.vue'

export default {
  props: ['title', 'error', 'placeholder', 'value'],
  components: { BasedFormField },
  computed: {
    dataValue: {
      get() {
        return this.value
      },
      set(value) {
        this.$emit('input', value)
      }
    }
  }
}
